import { Component, OnInit,EventEmitter, Output } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ClassService } from '@app/core/services/class.service';

@Component({
  selector: 'app-assessment-score-create',
  templateUrl: './assessment-score-create.component.html',
  styleUrls: ['./assessment-score-create.component.css']
})
export class AssessmentScoreCreateComponent implements OnInit {
  public assessmentscoreForm: FormGroup;
  public classLists: any = [];
  public assessmentList: any = [];
  public assessmentType: any = [];

  public search: any
  public direction: String;
  public page: any = 1;
  public submitted: boolean = false;

  @Output() onSave: any = new EventEmitter<any>()

  constructor(private formBuilder: FormBuilder, private _classService: ClassService) { }

  ngOnInit(): void {
    this.assessmentscoreForm = this.formBuilder.group({
      title: ["", [Validators.required]],
      classId: ["", [Validators.required]],
      assementId: ["", [Validators.required]],
      concept: ["", [Validators.required]],
      max_score: [0, [Validators.required]],
      assementType: ["", [Validators.required]],
      File: ["", [Validators.required]],
    });
    this.getClassLists();
    this.getAssessmentList();
    this.getAssessmentType();
  }

  getClassLists() {
    this._classService.classLists(this.search, this.direction, this.page).subscribe((res) => {
      this.classLists = res.classes;
    })
  }

  getAssessmentList() {
    this.assessmentList = [
      {
        "title": "Test1",
        "year": "Year 1",
        "description": "Lorem ipsum",
        "date": "2021-05-13T14:49:01.167Z"
      },
      {
        "title": "Test1",
        "year": "Year 1",
        "description": "Lorem ipsum",
        "date": "2021-05-13T14:49:01.167Z"
      },
      {
        "title": "Test1",
        "year": "Year 1",
        "description": "Lorem ipsum",
        "date": "2021-05-13T14:49:01.167Z"
      }
    ];
  }

  getAssessmentType() {
    this.assessmentType = [{ name: "Assement type 1" }, { name: "Assement type 2" },];
  }

  onClick() {
    let temp = {
      close: false,
    }
    this.onSave.emit(temp);
  }


  // convenience getter for easy access to form fields
  get f() { return this.assessmentscoreForm.controls; }

  onSubmit(){
    
  }
}
