import { Component, OnInit,Output,EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { YearService,ClassService } from "@core/services/serviceIndex";

@Component({
  selector: 'app-assessment-create',
  templateUrl: './assessment-create.component.html',
  styleUrls: ['./assessment-create.component.css']
})
export class AssessmentCreateComponent implements OnInit {
  public assessmentscoreForm: FormGroup;
  public classLists: any = [];
  public direction: String;
  public yearList: any = [];
  public page: any = 1;
  public submitted:boolean=false;
  public search: String;
  @Output() onSave: any = new EventEmitter<any>()

  constructor(private formBuilder: FormBuilder, private _yearService: YearService,
    private _classService: ClassService) { }

  ngOnInit(): void {
    this.assessmentscoreForm = this.formBuilder.group({
      class: ["", [Validators.required]],
      year: ["", [Validators.required]],
      address: ["", [Validators.required]],
    });

    this.getYearList();
    this.getClassLists();
  }

  getYearList() {
    this._yearService.getYearList()
      .subscribe(
        data => {
          this.yearList = ('years' in data) ? data.years : [];
          console.log(this.yearList);
          // this.years = this.yearList.map( d => d.name )
          //this.isLoading=false;
        },
        error => {
          console.log(error);
          // this.loading = false;
        }
      );
  }


  getClassLists() {
    this._classService.classLists(this.search, this.direction, this.page).subscribe((res) => {
      this.classLists = res.classes;
    })
  }

  onSubmit() {

  }

  // convenience getter for easy access to form fields
  get f() { return this.assessmentscoreForm.controls; }

  onClick(){
    let temp={
      close:false,
    }
    this.onSave.emit(temp);
  }

}
