import { AfterViewInit, Component, OnInit } from '@angular/core';
import { ActionLabel, Label } from "@enum/app_enum";
import { HomeService } from "@core/services/home.service";
@Component({
  selector: 'app-teacher-home',
  templateUrl: './teacher-home.component.html',
  styleUrls: ['./teacher-home.component.css']
})
export class TeacherHomeComponent implements OnInit,AfterViewInit {
  public tabLists: any = [ActionLabel.assessment, ActionLabel.assessmentScore];
  public activeIndex: any;
  public tabname: any = ActionLabel;

  public objectLabel = ["title", "year", "date", "description"];
  public limit: any = 10;
  public page: any = 1;
  public filter: any = {};
  public assessmentLists: any = {};
  public isScoreModal: boolean = false;
  public isAssessment: boolean = false;
  public searchData: String;
  public submitted: boolean = false;

  constructor(private _hservice: HomeService) { }

  ngOnInit(): void {
    this.filter = {
      sortColumn: this.objectLabel[0],
      sortDirection: "asc",
      index: 0
    };
  }

  ngAfterViewInit(){
    // this.getAssement();
  }


  tabChange(val: any) {
    this.activeIndex = val;
    if (this.activeIndex == 0)
      this.getAssement();
    else this.getAssementScore();
  }

  pageChanges(e: any) {
    console.log(e);
  }



  onFilter(filter: any) {
    this.filter = filter;
    this.getAssement();
  }

  getAssement() {
    this._hservice.getAssessmentsList(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection)
      .subscribe((assessment) => {
        this.assessmentLists.data = assessment.assements;
        this.assessmentLists.tableHeader = [Label.title, Label.year, Label.date, Label.description, Label.action];
        this.assessmentLists.actionBtns = [ActionLabel.edit, ActionLabel.delete];
        this.assessmentLists.objectLabel = ["title", "year", "date", "description"];
      })

  }

  getAssementScore() {
    this._hservice.getAssessmentScoreList(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection)
      .subscribe((score) => {
        this.assessmentLists.data = score.assement_scores;
        this.objectLabel = ["classId", "assementId", "concept", "max_score"];
        this.assessmentLists.tableHeader = [Label.class, Label.assessment, Label.concept, Label.maxScore, Label.action];
        this.assessmentLists.actionBtns = [ActionLabel.edit, ActionLabel.delete];
        this.assessmentLists.objectLabel = this.objectLabel;
      })

   
  }

  onSaveScore(data: any) {
    this.isScoreModal = data.close;
    this.isAssessment = data.close;
  }

  onSaveAssessment(data: any) {
    this.isScoreModal = data.close;
    this.isAssessment = data.close;
  }
  onClickScore(index: number) {
    this.isScoreModal = false;
    this.isAssessment = false;
    if (index == 1)
      this.isScoreModal = true;
    else this.isAssessment = true;
  }

}
